@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
*{
  margin: 0;
  padding: 0;
}
:root{
    --blue: #407BFF;
  }

.navbar__ppal{
    height: 100px;
    box-sizing: content-box;
}

.navbar__container{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    height: 100%;
}

.navbar__logo{
    padding-left: 10%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.logo__text{
    margin: 0;
    font-size: 25px;
} 

.navbar__buttons{
    box-sizing: content-box;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.navbar__buttons button{
    font-size: 15px;
    margin-right: 20px;
}

.quarkName{
    color: #407BFF;
    color: var(--blue)
}

.navbar__button-main{
    background-color: #407BFF;
    background-color: var(--blue);
}
:root{
    --blue: #407BFF;
  }

.hero{
    padding: 50px 0;
    height: 700px;
}

.container{
    height: 100%;
}

.hero__container{
    max-height: 500px;
}

.hero__container-row{
    height: 100%;
}

.hero__image{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.imagenBanner {
    width: 100%;
}

.info__col{
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: flex-start;
    height: 100%;
}

.NameHero{
    margin-bottom: 10px;
}

.NameHero-quark{
    color: #407BFF;
    color: var(--blue);
    font-size: 75px;
    font-family: 'Source Sans Pro', sans-serif;
}

.NameHero-courses{
    font-size: 75px;
    font-family: 'Source Sans Pro', sans-serif;
}


.hero__text{
    font-family: 'Nunito', sans-serif;
    color: #808080
}

.hero__button{
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 20px;
    height: 50px;
    width: 300px;
    background-color: #407BFF;
    background-color: var(--blue);
}

