:root{
    --blue: #407BFF;
  }

@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');

.hero{
    padding: 50px 0;
    height: 700px;
}

.container{
    height: 100%;
}

.hero__container{
    max-height: 500px;
}

.hero__container-row{
    height: 100%;
}

.hero__image{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.imagenBanner {
    width: 100%;
}

.info__col{
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: flex-start;
    height: 100%;
}

.NameHero{
    margin-bottom: 10px;
}

.NameHero-quark{
    color: var(--blue);
    font-size: 75px;
    font-family: 'Source Sans Pro', sans-serif;
}

.NameHero-courses{
    font-size: 75px;
    font-family: 'Source Sans Pro', sans-serif;
}


.hero__text{
    font-family: 'Nunito', sans-serif;
    color: #808080
}

.hero__button{
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 20px;
    height: 50px;
    width: 300px;
    background-color: var(--blue);
}
